<template>
  <div class="p2">
    <p class="pp2">请确认下表与您填报的申报数据是否一致？</p>
    <P class="pp1">印花税纳税申报表</P>
    <table class="yhs" cellpadding="0" width="100%" cellspacing="0" border="1">
      <tr>
        <td style="width:150px;">纳税人识别号</td>
        <td colspan="2">{{com.firm_code}}</td>
        <td colspan="2">纳税人名称</td>
        <td colspan="2">{{com.firm_name}}</td>
        <td>申报日期</td>
        <td colspan="2">{{com.month}}-{{new Date().getDate()}}</td>
      </tr>
      <tr>
        <td>税款所属期起</td>
        <td colspan="2">{{com.month}}-01</td>
        <td colspan="2">税款所属期止</td>
        <td colspan="2">{{com.month}}-30</td>
        <td>申报类型</td>
        <td colspan="2">正常申报</td>
      </tr>
      <tr>
        <td>纳税人类型</td>
        <td colspan="2">单位</td>
        <td colspan="2">登记注册类型</td>
        <td colspan="2">{{com.firm_type}}</td>
        <td>所属行业</td>
        <td colspan="2">{{com.firm_trade}}</td>
      </tr>
      <tr>
        <td>身份证件类型</td>
        <td colspan="2">居民身份证</td>
        <td colspan="2">身份证件号码</td>
        <td colspan="2">{{com.firm_legal_person_id}}</td>
        <td>联系方式</td>
        <td colspan="2">{{com.firm_telephone}}</td>
      </tr>
      <tr>
        <td rowspan="3">应税凭证名称</td>
        <td rowspan="2">计税金额或件数</td>
        <td colspan="2">核定征收</td>
        <td rowspan="2">适用税率</td>
        <td rowspan="2">本期应纳税额</td>
        <td rowspan="2">本期已缴税额</td>
        <td colspan="2">本期减免税额</td>
        <td rowspan="2">本期应补（退）税额</td>
      </tr>
      <tr>
        <td>核定计税依据</td>
        <td>核定比例</td>
        <td>减免性质代码</td>
        <td>减免税额</td>
      </tr>
      <tr>
        <td>1</td>
        <td>2</td>
        <td>3</td>
        <td>4</td>
        <td>5=1*4 or 5=1*3*4 or 5=2*4</td>
        <td>6</td>
        <td>7</td>
        <td>8</td>
        <td>9=5-6-8</td>
      </tr>
      <tr v-for="(item,index) in setting" v-if="item.taxes_status == taxes" class="addtr">
        <td>{{item.taxable_name}}</td>
        <td style="background-color:#FFF;">
          <input :name="index+1 + '_1'" readonly/>
        </td>
        <td>
          <input :name="index+1 + '_2'" readonly/>
        </td>
        <td>
          <input :name="index+1 + '_3'" readonly class="tiaochu"/>
        </td>
        <td>
          <input :name="index+1 + '_4'" readonly class="tiaochu"/>
        </td>
        <td>
          <input :name="index+1 + '_5'" readonly/>
        </td>
        <td style="background-color:#FFF;">
          <input :name="index+1 + '_6'" readonly/>
        </td>
        <td style="background-color:#FFF;">
          <input :name="index+1 + '_7'" readonly type="text"/>
        </td>
        <td style="background-color:#FFF;">
          <input :name="index+1 + '_8'" readonly/>
        </td>
        <td>
          <input :name="index+1 + '_9'" readonly/>
        </td>
      </tr>
      <tr>
        <td>合计</td>
        <td>--</td>
        <td>--</td>
        <td>--</td>
        <td>--</td>
        <td><input name="h_5" readonly value="0.00"/></td>
        <td><input name="h_6" readonly value="0.00"/></td>
        <td>--</td>
        <td><input name="h_8" readonly value="0.00"/></td>
        <td><input name="h_9" readonly value="0.00"/></td>
      </tr>
    </table>
    <div style="margin-top: 15px" class="uk-text-center">
      <router-link to="/transfer/p43" @click.native="declare">
        <button class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 确认</button>
      </router-link>
      <router-link to="/transfer/p41">
        <button class="uk-button uk-button-primary"><i class="uk-icon-close"></i> 返回</button>
      </router-link>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 41,  //当前表格的名称编号
        setting: [],
        com: [],
        taxes: this.$store.state.isConfirmed.taxes_status
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        this.com = this.$store.state.companyCurrent.gscompany
        let arr = this.$store.state.companyCurrent.yhs
        var attr = []
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].taxes_status == this.taxes) {
            attr.push(arr[i])
          }
        }
        this.setting = attr
        setTimeout(() => {
          if (this.formData) {
            this.recoverFormData()
          }
        }, 150)
      },
      declare() {
        this.$store.commit('saveConfirm', { name: 'YHS', data: true })
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
  }

  .pp1 {
    font-weight: bold;
    font-size: 24px;
  }

  .pp2 {
    font-size: 24px;
    font-weight: bold;
    text-align: left;
    color: red;
  }

  .yhs input {
    width: 100%;
    height: 100%;
    border: 0px;
  }
</style>



